<template>
  <div class="container">
    <Header>
      <div slot="title">{{title}}</div>
    </Header>
    <div style="height: .92rem"/>
    <selected-content
      :storeId="storeId"
      :storeSign="storeSign"
      :shareId="''"
      :pageId="pageId"
      :storeSceneData="storeSceneData"
      :modelList="modelList"
      :isCustomPage="true"
    />
  </div>
</template>
<script>
import {
  getStoreSign,
  getCustomPageModel,
  getStoreSceneNew,
} from '@/http/service/shop';
import Header from '@/components/header'
import selectedContent from '../component/top-bar/selected/barSelected';

let ua = 'H5' // ua决定跳转链接与跳转方式
// if (utils.isSyBird()) {
//   ua = 'Flutter'
// } else if (utils.isSyBirdMP()) {
//   ua = 'WeChat'
// }

const jumpType = {
  'H5': {
    val: 5,
    urlKey: 'detailUrl'
  },
  'WeChat': {
    val: 22,
    urlKey: 'weChatDetailUrl'
  },
  'Flutter': {
    val: 16,
    urlKey: 'flutterDetailUrl'
  },
}[ua]

export default {
  components: {
    selectedContent,
    Header,
  },
  data() {
    return {
      storeId: '',
      pageId: '',
      modelList: [],
      title: '',
      storeSign: {
        showLogo: 1,
        logoUrl: 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/uplus_logo.png', // 店铺图标
        storeType: 1, //  0-非旗舰店 1-旗舰店 2-售后店 3-生态店 4-品类店 5-品旗店 6-001号店
        shopStoreCategory: '', // 店铺二级分类页面 01-乡镇店 11-智家店 12-三翼鸟店 13-综合店 31-生态店 32-家装 33-智能互联 34-安防 35-生活家电
        storeBgImg: '', // 头部背景
        storeCode: '', // 店铺88码
        storeAddress: '', // 店铺地址
        latitude: '',
        longitude: '',
        storeName: '',
        chosenTab: 1, // 精选是否显示
      },
      storeSceneData: {}
    };
  },
  computed: {
    location() {
      return this.$store.getters['getlocationInfo']
    },
  },
  created() {
    // this.$route.meta.title = '自定义页面';
    this.storeId = this.$route.query.storeId;
    this.pageId = this.$route.query.pageId;
    this.getStoreSign();
    this.getModels();
  },
  methods: {
    getModels() {
      const params = {
        storeId: this.storeId,
        streetId: this.location.townshipId,
        provinceId: this.location.provinceId,
        cityId: this.location.cityId,
        districtId: this.location.districtId,
        pageId: this.pageId,
        modelStatus: 1,
      };
      getCustomPageModel(params).then(res => {
        console.log('精选模块：', res);
        if (res.success) {
          // this.$route.meta.title = res.data.pageName || '';
          this.title = res.data.pageName;
          console.log('title:', res.data.pageName);
          if (res.data.modelList && res.data.modelList.length) {
            res.data.modelList.sort((a, b) => a.modelSort - b.modelSort);
            this.modelList = res.data.modelList;
          }
        } else {
          this.$toast(res.message || '获取数据失败，请稍后重试');
        }
      });
    },
    getStoreSign() {
      let longitude = ''
      let latitude = ''
      if (this.location) {
        longitude = this.location.longitude
        latitude = this.location.latitude
      }
      getStoreSign({
        storeId: this.storeId,
        longitude,
        latitude,
        shareId: sessionStorage.getItem('shareUserId') || '',
        mdCode: sessionStorage.getItem('shareMdCode') || ''
      }).then(res => {
        this.isSkeletonLoading = false;
        if (res.success) {
          this.storeSign = {
            ...this.storeSign,
            ...res.data
          };
          if (res.data.synStoreType !== '9') this.getSceneList() // 店铺升级为三翼鸟店铺时才会请求场景数据
        } else {
          console.log('获取店招模块失败');
        }
      });
    },
    getSceneList() {
      const param = {
        current: 1,
        size: 4,
        storeId: this.storeSign.storeId,
      };
      getStoreSceneNew(param).then(res => {
        if (res.success) {
          let sceneUrl = ''

          if (res.data[`senceList${ua}Url`]) {
            sceneUrl = res.data[`senceList${ua}Url`]
            if (!sceneUrl.includes('?')) {
              sceneUrl += '?'
            } else {
              sceneUrl += '&'
            }
            sceneUrl += `userId=${this.$store.state.ucId ? this.$store.state.ucId : ''}&userAccessToken=${this.$store.getters.getUserCenterToken}`
          }

          this.storeSceneData = {
            ...res.data,
            storeSenceUrl: this.$util.injectISOAddressCodeToURL(sceneUrl, this.$store.getters.getlocationInfo),
            jumpType: jumpType.val,
            sceneUrlKey: jumpType.urlKey
          }
          console.log('场景数据', this.storeSceneData)
        } else {
          this.$toast(res.message || '获取数据失败，请稍后重试');
        }
      });
    },
  },
}
</script>
<style lang="scss" scoped>
.container {
  display: flex;
  flex-direction: column;
  width: 100%;
}
</style>
